nav {
  position: fixed;
  color: white;
  top: 0;
  right: 0;
  z-index: 10000;
  font-size: 18px;
}

nav ul a {
  position: relative;
  text-decoration: none;
  color: var(--color7);
  margin: 0 0.8rem;
}

nav ul a li {
  position: relative;
}

nav ul a li::after {
  content: "";
  position: absolute;
  background-color: var(--color5);
  height: 3px;
  bottom: -0.4rem;
  left: -0.2rem;
  right: -0.2rem;
  transform: scale(0, 1);
  transition: transform ease-out 0.5s;
}

nav ul a li:hover::after,
nav ul a li.active::after {
  transform: scale(1, 1);
}

nav ul {
  list-style-type: none;
}

nav li {
  display: inline;
}
